import { Request } from './requestConfig';

/**
 * post
 * @param {string} url 
 * @param {object} data 
 */
export const Post = (url, data = {}) =>
  Request(url, {
    method: 'post',
    data,
  });

/**
 * get
 * @param {string} url 
 * @param {object} data 
 */
export const Get = (url, data = {}) =>
  Request(url, {
    params: {
      ...data,
    },
  });
