/**
 * request 
 *  api : https://github.com/umijs/umi-request
 */
import request, { extend } from 'umi-request';
import { notification, message } from 'antd';

const codeMessage = {
  200: '',
  201: '',
  202: '',
  204: '',
  400: '',
  401: '',
  403: '',
  404: '',
  406: '',
  410: '',
  422: '',
  500: '',
  502: '',
  503: '',
  504: '',
};

/**
 * 
 */

const errorHandler = error => {
  const { response } = error;

  if (response && response.status) {
    const errorText = codeMessage[response.status] || response.statusText;
    const { status, url } = response;
    notification.error({
      message: ` ${status}: ${url}`,
      description: errorText,
    });
  } else if (!response) {
    notification.error({
      description: '',
      message: '',
    });
  }

  return response;
};
/**
 * 
 */
const statusCode = {
  success: [200],
  message: [300],
  error: [500],
};

const messages = {
  500: '',
};

request.interceptors.response.use(async (response, options) => {
  const data = await response.clone();
  const res = await data.json();
  if (!statusCode.success.includes(res.status)) {
    message.error(`${res.message || messages[res.status]}`);
  }
  return response;
});
/**
 * 
 */
const isPro = process.env.NODE_ENV === 'production';

const isHome = process.env.WORK === 'home';

// 
const proBaseUrl = 'http://192.168.31.87:9000';

// 
// home 
// const devBaseUrl = `http://192.168.31.87:9000`;
const devBaseUrlHome = '/home';

// company ;
const devBaseUrlCompany = '/company';

const devBaseUrl = isHome ? devBaseUrlHome : devBaseUrlCompany;

export const baseUrl = isPro ? proBaseUrl : devBaseUrl;

request.interceptors.request.use((url, options) => ({
  url: `${baseUrl}${url}`,
  options: {
    ...options,
    interceptors: true,
  },
}));

export const Request = extend({
  errorHandler,
  timeout: 1000 * 60,
  credentials: 'include', // cookie
});
